
import { mapGetters } from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XButton
},
computed: {
...mapGetters([
'sidebar',
'language'
])
},
data() {
return {
showOne: false,
showTwo: false
}
},
methods: {
jump2Next() {
this.$router.push({path: '/swiper-img'})
},
addText() {
setTimeout(() => {
if (!this.showOne) {
this.showOne = true
this.addText()
} else if (!this.showTwo) {
this.showTwo = true
}
}, 1000)
}
},
mounted() {
this.$nextTick(function () {
this.addText()
})
}
}



